@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
    /* background-image:linear-gradient(rgb(113, 113, 212), rgb(119, 119, 240)) */
    background: rgb(1,127,180);
    background: linear-gradient(184deg, rgba(1,127,180,1) 0%, rgba(1,132,187,1) 17%);
    padding: 0px;
    margin:0px;
    height: 100%;
    width: 100%;
    font-size: 40px;
    /* overflow: hidden; */
}
ul{
    list-style-type: none;
}
/* img{
    height: 50%;
    width: 50%;
} */
li.AA{
    background-image: url('https://disease.sh/assets/img/flags/pk.png');
    background-position: center; /* Center the image */
    background-repeat: no-repeat; /* Do not repeat the image */
    /* background-size: cover; */
}
.cont{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    /* background-color: red; */
    margin-top: 100px;
    color: green;
}

h2{
    display: flex;
    justify-content: center;
    color:rgb(0, 0, 255);
    font-family: 'Source Sans Pro', sans-serif;
    /* font-size: 30px; */
    padding: 30px;
    /* border: 2px solid red; */
    /* background: rgb(1, 127, 180); */
    background: linear-gradient(184deg, rgba(1, 127, 180, 0.4500175070028011) 0%, rgba(0, 111, 158, 0.4500175070028011) 17%);
    -webkit-backdrop-filter: blur(2px);
            backdrop-filter: blur(2px);
    -webkit-backdrop-filter: brightness(80%);
            backdrop-filter: brightness(80%);
    border-radius: 80px;
    
}
ul{
    padding: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;

}
ul li{
    width: 22rem;
    height: 12rem;
    display: flex;
    flex-wrap: wrap;
}
.card{
    /* border: 2px solid red; */
    margin: 7px;
    /* width: 29%;
    height: 29%; */
    color: rgb(0, 0, 255);
    font-family: 'Source Sans Pro', sans-serif;
    /* background: rgb(1, 127, 180); */
    background: linear-gradient(184deg, rgba(1, 127, 180, 0.4500175070028011) 0%, rgba(0, 111, 158, 0.4500175070028011) 17%);
    -webkit-backdrop-filter: blur(100px);
            backdrop-filter: blur(100px);
    -webkit-backdrop-filter: brightness(80%);
            backdrop-filter: brightness(80%);
    display: flex;
    text-align: center;
    justify-content: center;
    border-radius: 50px;
}
@media only screen and (max-width: 300px) {
    body {
        font-size: 30px;
    }
    p.card_name{
    margin-top: 50px;
    }
    .cont{
        margin-top: 115px;
    }
  }

